body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0%;
    padding: 0%;
}
.myname {
    font-family: cursive;
}
.all-links{
    text-decoration: none;
    color: rgba(255, 255, 255, 0.699);
}
.all-links LinkScroll{
    text-decoration: none;
}
.all-links:hover{
    color: white;
}


#Aboutme{
    color: white;
}
.main{
    background: linear-gradient(#a29bfe,#74b9ff);
}
.containt-heading{
    text-align: center;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-name: s;
            animation-name: s;
}
@-webkit-keyframes s{
    from{
        margin-left: 20%;
    }
    to{
        margin-left: 0%;
    }
}
@keyframes s{
    from{
        margin-left: 20%;
    }
    to{
        margin-left: 0%;
    }
}
.hobys{
    display: flex;
}

@media screen and (max-width: 600px){

    .hobies-image-1{
        width: 200px;
        
    }
    .hobies-image-2{
        width: 200px;
        
    }
}
@media screen and (max-width: 900px) {
    
    .hobys{
        flex-direction: column;
    }
}

.skillscard{
    display: flex;
}
.jsimage{
    width: 100px;
}
#heading-of-skills{
    text-align: center;
    color: white;
}
.first-line-skill{
    display: flex;
    justify-content: space-around;
}
.second-line-skill{
    display: flex;
    justify-content: center;
    align-items: center;
}
@media screen and (max-width: 600px ){
    .first-line-skill{
        flex-direction: column;
        margin: 10px;
        padding: 10px;
    }
}
.start-word{
    background:linear-gradient(#380036,#0CBABA)
}
.first-heading{
    height: 100vh;
    display: grid;
    place-items:center;
    color: white;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    letter-spacing: 5px;
    word-spacing: 10px;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-name: slidein;
            animation-name: slidein;
}
@-webkit-keyframes slidein {
    from {
        margin-right: 50%;
    }
    to{
        margin-right: 0%;
    }
}
@keyframes slidein {
    from {
        margin-right: 50%;
    }
    to{
        margin-right: 0%;
    }
}
 .apifetch{
    display: flex;
    justify-content: space-around;
} 
@media screen and (max-width: 600px){
    .apifetch{
        align-items: flex-start;
        flex-direction:column;
    }
}
